
import { Layout, Button, notification, Form, Input, Select, Checkbox, Row, Col } from 'antd';
import { useState} from 'react';
import { useRef} from 'react';
import { useNavigate } from "react-router-dom";



export default function Login(props) {
    const { Content } = Layout;
    const { Option } = Select;
    const{correo,setCorreo} = useState("");
    const{pass,setPass} = useState("");
    let navigate = useNavigate();

    let refUsuario=useRef(null);

    function handleLogin(){
        refUsuario.current.focus();
        console.log(refUsuario.current.value);
    }

    const formItemLayout = {
        labelCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 9,
            },
        },
        wrapperCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 6,
            },
        },
    };

    const tailFormItemLayout = {
        wrapperCol: {
            xs: {
                span: 24,
                offset: 0,
            },
            sm: {
                span: 16,
                offset: 11,
            },
        },
    };

    const openNotification = type => {
        notification[type]({
            message: 'Acceso permitido',
            description:
                'Lo redigiremos a la sección productos',
            onClick: () => {
                console.log('Notification Clicked!');
            },
        });
    };


    return (

        <Content style={{ margin: '15px' }}>
            <header> <center><h1>S.A.R.T. DELUXE ENTERPRICES</h1></center> </header>
            <legend><center><h2>ACCESO</h2></center></legend>
            <br/><br/>
            <Form {...formItemLayout}>
                <Form.Item  label='Correo electrónico' required>
                    <Input type='email' placeholder='Ingrese correo'
                    value={correo} 
                    onChange={e=>setCorreo(e.target.value)}
                    />
                </Form.Item>
                <Form.Item label="Contraseña" required>
                    <Input type="password" placeholder="Ingrese contraseña" 
                    />
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button  
                    className='btn' type="primary" size='large'  
                    >
                        Acceder
                    </Button>
                    <div className='card-footer'>
                        <span>¿Olvidó su contraseña?</span><br/>
                        <a href='https://'>Recuperar contraseña</a>
                    </div>
                </Form.Item>
            </Form>
        </Content >
    );
}
