import { Layout, Row, Col } from 'antd';
import img1 from './img/integridad.jpg'
import img2 from './img/pasion.png'
import img3 from './img/respon2.jpg'
import img4 from './img/ropa4.jpg'
import img5 from './img/transpariencia.jpg'

const { Content } = Layout;

export default function nosotros() {
    return (
        <Content style={{ margin: '20px' }}>
            <Row justify='center'>
                <Row justify='center'>
                    <h1>
                        <center><font size="8">NOSOTROS</font></center>
                    </h1>
                    <img src={img4} width="100%"></img>

                    <div>
                        S.A.R.T es una familia de marcas y negocios que hace posible que los clientes de todo el mundo se
                        expresen a través de la moda y el diseño, y elijan un estilo de vida más sostenible. Creamos valor para las
                        personas y la sociedad en general entregando nuestra oferta al cliente y desarrollando
                        con un enfoque en el crecimiento sostenible y rentable.
                    </div>

                    <h2 style={{ margin: '20px' }}>NUESTROS VALORES</h2>
                </Row>
                <Row gutter={100} justify='center'>
                    <Col xs={24} sm={12} md={6}><h2>Transparencia</h2><img src={img5} width="300" height="200"></img></Col>
                    <Col xs={24} sm={12} md={6}><h2>Responsabilidad </h2><img src={img3} width="300" height="200"></img></Col>
                    <Col xs={24} sm={12} md={6}><h2>Pasión</h2><img src={img2} width="300" height="200"></img></Col>
                    <Col xs={24} sm={12} md={6}><h2>Integridad</h2><img src={img1} width="300" height="200"></img></Col>
                </Row>
            </Row>
            <h2 style={{ margin: '20px' }}>DATOS DE LA EMPRESA</h2>
            <td>
                <ul type="square">
                    <li>RAZON SOCIAL: S.A.R.T. DELUXE ENTERPRICES S.A.</li>
                    <li>RUC:    12345678901</li>
                    <li>RUBRO: Textil y moda</li>
                    <li>DIRECCIÓN: Calle Los Girasoles 1800 San Isidro Lima</li>
                    <li>TELEFONO: 01-407629</li>
                </ul>
            </td>
        </Content>
    );
}