import React, { useEffect, useState } from "react";
import { Route, Routes } from "react-router-dom";
import { debounce } from 'lodash';
import Entrada from '../routes/entrada'
import Main from '../routes/main';

export default function Setup() {

    const [mobile, setMobile] = useState(false);
    const [tablet, setTablet] = useState(false);

    useEffect(() => {
        window.addEventListener('resize', debounce(updateWidth, 150));
    }, [])

    const updateWidth = () => {
        if (window.outerWidth > 768) {
            setMobile(false);
            if (window.outerWidth <= 847) {
                setTablet(true);
            } else {
                setTablet(false);
            }
            
        } else if (window.outerWidth <= 768) {
            setMobile(true);
            setTablet(false);
        }
    };

    return (
        <React.Fragment>
            <Routes>
                <Route path="/" element={<Entrada mobile={mobile} tablet={tablet}/>} />
                <Route path="main" element={<Main mobile={mobile} tablet={tablet}/>} />
            </Routes>
        </React.Fragment>
    );
}