import { Layout } from 'antd';
import img1 from './img/1.jpg'
import img2 from './img/2.png'
import img3 from './img/3.JPG'
import './index.css'

const { Content } = Layout;

export default function servicios(props) {
    return (
            <Content style={{textAlign: 'center', margin: '20px'}}>
                <h1><font size="8"> NUESTROS SERVICIOS</font></h1>
                <p className="after">Siempre mejorando para ti</p>
                <div className="servicios">
                    <div className="caja-servicios">
                        <img src={img2} alt="" width={props.mobile? '100%': 'auto'}/>
                            <h4>De la mano con los clientes ante alguna duda</h4>
                            <p>El mantener una conexion directa con el cliente nos ayuda a poder ser parte de su hábito de vestimenta.</p>
                    </div>
                    <div className="caja-servicios">
                        <img src={img1} alt="" width={props.mobile? '100%': 'auto'}/>
                            <h4>Ofreciendo servicio de calidad</h4>
                            <p>Un cliente satisfecho y a la moda, es lo unico que se tiene en mente al momento de ofrecer nuestros servicios.</p>
                    </div>
                    <div className="caja-servicios">
                        <img src={img3} alt="" width={props.mobile? '100%': 'auto'}/>
                            <h4>El trabajo de equipo</h4>
                            <p>El trabajo en union de nuestros empleados fortalece el lazo de comapañerismo consiguiendo asi una union solida.</p>
                    </div>
                </div>
            </Content>
            );
}