import { Layout, List, Button, Descriptions, notification } from 'antd';
import { DeleteTwoTone } from '@ant-design/icons';
import './index.css'

const { Content } = Layout;


export default function Compra(props) {

    const openNotification = type => {
        notification[type]({
          message: 'Compra realizada con exito',
          description:
            'Estamos procesando su compra.',
          onClick: () => {
            console.log('Notification Clicked!');
          },
        });
      };

    let precioTotal = () => props.carrito.reduce((total, item) => total + item.precio , 0);

    return (
        <Content style={{ margin: '80px' }}>
            <h2>Datos del usuario</h2>
            <Descriptions>
                <Descriptions.Item label="UserName">Zhou Maomao</Descriptions.Item>
                <Descriptions.Item label="Telephone">1810000000</Descriptions.Item>
                <Descriptions.Item label="Live">Hangzhou, Zhejiang</Descriptions.Item>
                <Descriptions.Item label="Remark">empty</Descriptions.Item>
                <Descriptions.Item label="Address">
                    No. 18, Wantang Road, Xihu District, Hangzhou, Zhejiang, China
                </Descriptions.Item>
            </Descriptions>
            <h2>Lista de productos</h2>
            <List
                itemLayout="horizontal"
                dataSource={props.carrito}
                locale = {{emptyText: 'No hay productos en la lista'}}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            title={<>{item.descripcion} <DeleteTwoTone twoToneColor="#eb2f96" title='Eliminar' style={{ cursor: 'pointer' }} onClick={() => props.deleteItemsCarrito(item)} /></>}
                            description={<p>${item.precio}</p>}
                        />
                    </List.Item>
                )}
            />
            <h2>Total a pagar: ${precioTotal()}</h2>
            <Button type="primary" disabled={precioTotal() <= 0} size='large' onClick={() => {
                    openNotification('success')
                    return props.compraRealizada();
                }}>
                Confirmar Compra
            </Button>
        </Content>
    );
}