import { Layout, Row, Menu, Radio, Divider } from 'antd';
const { Content, Sider } = Layout;

export default function ContenidoSider(props) {

    const options = [
        { label: 'Hombre', value: 'hombre' },
        { label: 'Mujer', value: 'mujer' },
    ];


    const menuItems = [
        { label: 'Givenchi', key: 'givenchi' }, // remember to pass the key prop
        { label: 'Dolce & Gabbana', key: 'dgavana' }, // which is required
        { label: 'Valentino', key: 'valentino' }, // which is required
        { label: 'Versace', key: 'versace' }, // which is required
    ];

    return (
        <>
            <Divider>Genero</Divider>
            <Radio.Group
                options={options}
                onChange={props.onChangeRadioGenero}
                value={props.genero}

            />
            <Divider>Marcas</Divider>
            <Menu onClick={props.onClickMarca} selectedKeys={[props.marca]} items={menuItems} />
        </>
    );
}