import {Button, Form, Input, Layout, notification} from 'antd';

const {Content} = Layout;

export default function Contacto(props) {

    const formItemLayout = {
        labelCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 6,
            },
        },
        wrapperCol: {
            xs: {
                span: 24,
            },
            sm: {
                span: 14,
            },
        },
    };

    const tailFormItemLayout = {
        wrapperCol: {
            xs: {
                span: 24,
                offset: 0,
            },
            sm: {
                span: 14,
                offset: 6,
            },
        },
    };

    const openNotification = type => {
        notification[type]({
            message: 'Información enviada con exito',
            description:
                'Lo redigiremos a la sección productos',
            onClick: () => {
                console.log('Notification Clicked!');
            },
        });
    };

    return (
        <Content style={{margin: '15px'}}>
            <h1 style={{textAlign: 'center'}}>
                <font size="8">CONTACTANOS</font>
            </h1>
            <h3 style={{textAlign: 'center'}}>Escríbenos y en breve los pondremos en contacto contigo</h3>
            <Form {...formItemLayout} style={{margin: '15px'}}>
                <Form.Item label="Nombre" required>
                    <Input placeholder="Escribe tu nombre" />
                </Form.Item>
                <Form.Item label="Correo electrónico" required>
                    <Input placeholder="Ingrese correo electrónico" />
                </Form.Item>
                <Form.Item label="Telefono" required>
                    <Input placeholder="Ingrese telefono" />
                </Form.Item>
                <Form.Item label="Asunto" required>
                    <Input placeholder="Escribe un asunto" />
                </Form.Item>
                <Form.Item label="Mensaje" required>
                    <Input.TextArea placeholder="Deja aquí tu comentario..." rows={4} />
                </Form.Item>
                <Form.Item {...tailFormItemLayout}>
                    <Button type="primary" size='large' onClick={() => {
                        openNotification('success')
                        return props.contactoRealizado();
                    }}>
                        Enviar
                    </Button>
                </Form.Item>
            </Form>
        </Content>
    );
}