import { Card, Col, Layout, Row} from 'antd';
import cr from './img/cr.jpeg'
import ge from './img/ge.jpeg'
import da from './img/da.jpeg'
import le from './img/le.jpeg'
import './equipo.css'
const { Content } = Layout;
const {Meta} = Card;

export default function Equipo() {

    return (
            <Content style={{margin: '15px', textAlign: 'center'}}>
                <h1><font size="8"> NUESTRO EQUIPO</font></h1>
                <p className="after">Conoce a la gente asombrosa y creativa</p>
                <Row gutter={[32, 24]}>
                    <Col className="item" xs={24} sm={12} md={6}>
                        <Card
                            hoverable
                            cover={<img alt="example" src={ge}/>}
                        >
                            <Meta title="Huaman Cubas Heidi Vanessa" description="Diseño la página servicios"/>

                        </Card>
                    </Col>

                    <Col className="item" xs={24} sm={12} md={6}>
                        <Card
                            hoverable
                            cover={<img alt="example" src={da}/>}
                        >
                            <Meta title="Daniel Ulloa Canales" description="Diseño la página nosotros"/>

                        </Card>
                    </Col>

                    <Col className="item" xs={24} sm={12} md={6}>
                        <Card
                            hoverable
                            cover={<img alt="example" src={le}/>}
                        >
                            <Meta title="Leonel Guerrero Orihuela" description="Diseño la página equipo"/>

                        </Card>
                    </Col>

                    <Col className="item" xs={24} sm={12} md={6}>
                        <Card
                            hoverable
                            cover={<img alt="example" src={cr}/>}
                        >
                            <Meta title="Christian Carlo Ventura Ríos" description="Diseño la página inicio, registro y productos."/>
                        </Card>
                    </Col>
                </Row>
            </Content >
    );
}