import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import { Badge, Button, Col, Layout, Menu, Popover, Row } from 'antd';
import { MenuOutlined, ShoppingCartOutlined,FacebookFilled,TwitterOutlined,InstagramOutlined } from '@ant-design/icons';
import ChatBot from 'react-simple-chatbot';
import Productos from '../productos';
import Registro from '../registro';
import Servicios from '../servicios';
import Compra from '../compra';
import Nosotros from '../nosotros';
import Equipo from '../equipo';
import Contacto from '../contacto';
import Login from '../login';
import moda2 from '../../img/moda2.jpg';
import './index.css'

const { Header, Footer } = Layout;


const steps = [
    {
        id: '1',
        message: 'Cual es tu nombre?',
        trigger: '2',
    },
    {
        id: '2',
        user: true,
        trigger: '3',
    },
    {
        id: '3',
        message: 'Hola {previousValue}, es un gusto conocerte!',
        end: true,
    },
];

const menuNavegacion = [
    { label: 'Inicio', key: 'inicio' },
    { label: 'Login', key: 'login' },
    { label: 'Registro', key: 'registro' },
    { label: 'Productos', key: 'productos' },
    { label: 'Servicios', key: 'servicios' },
    { label: 'Nosotros', key: 'nosotros' },
    { label: 'Equipo', key: 'equipo' },
    { label: 'Contacto', key: 'contacto' },
];

export default function Main(props) {

    const [pagina, setPagina] = useState('productos');
    const [carrito, setCarrito] = useState([]);
    const [showPopover, setShowPopover] = useState(false);
    let navigate = useNavigate();

    const handleScroll = () => {
        setTimeout(() => {
            window.scrollTo({ top: 0, behavior: 'smooth' })
        }, 500)
    }

    const onClickNavegacion = e => {
        console.log('menu clicked', e);
        if (e.key === 'inicio') {
            navigate('/');
        } else {
            setPagina(e.key);
        }
    };

    const addItemsCarrito = (producto) => {
        console.log(producto);
        setCarrito([...carrito, { ...producto, id: carrito.length + 1 }])
    };

    const deleteItemsCarrito = (producto) => {
        let filteredArray = carrito.filter(item => item.id !== producto.id)
        setCarrito(filteredArray);
    };

    const showCompra = () => {
        setPagina('compra');
    }

    const compraRealizada = () => {
        setPagina('productos');
        setCarrito([]);
        handleScroll();
    }

    const accesoPermitido = () => {
        setTimeout(() => setPagina('productos'), 2000);
    }

    const registroRealizado = () => {
        setTimeout(() => setPagina('productos'), 2000);
    }

    const contactoRealizado = () => {
        setTimeout(() => setPagina('productos'), 2000);
    }

    const paginaRender = (mobile) => {
        let paginaRender;
        if (pagina === 'productos') {
            paginaRender = <Productos addItemsCarrito={addItemsCarrito} mobile={mobile} />
        } else if (pagina === 'registro') {
            paginaRender = <Registro registroRealizado={registroRealizado} mobile={mobile} />
        } else if (pagina === 'login') {
            paginaRender = <Login accesoPermitido={accesoPermitido} mobile={mobile} />
        } else if (pagina === 'servicios') {
            paginaRender = <Servicios mobile={mobile} />
        } else if (pagina === 'compra') {
            paginaRender =
                <Compra carrito={carrito} deleteItemsCarrito={deleteItemsCarrito} compraRealizada={compraRealizada}
                    mobile={mobile} />
        } else if (pagina === 'nosotros') {
            paginaRender = <Nosotros mobile={mobile} />
        } else if (pagina === 'equipo') {
            paginaRender = <Equipo mobile={mobile} />
        } else if (pagina === 'contacto') {
            paginaRender = <Contacto mobile={mobile} contactoRealizado={contactoRealizado} />
        }
        return paginaRender;
    }

    const handleShowMenu = () => {
        if (showPopover) {
            setShowPopover(false);
        } else {
            setShowPopover(true);
        }
    }

    return (
        <Layout>
            <Header id='header'>
                <Row align='middle' style={{ height: '64px' }}>
                    {/* <div className="logo" style={{ float: 'left' }}><img alt="example" src={moda2} width='37px' /></div> */}
                    <Col flex={!(props.mobile || props.tablet) ? "100px" : "auto"} align='center'>
                        <img alt="example" src={moda2} width='37px' />
                    </Col>
                    {!(props.mobile || props.tablet) && <Col flex="auto" className="menu-row">
                        <Menu mode="horizontal" items={menuNavegacion} onClick={onClickNavegacion}
                            selectedKeys={[pagina]} />
                        {/* {pagina !== 'registro' && <div className="logo" style={{ float: 'right', padding: '10px 0' }}><Badge count={carrito.length}><Button onClick={showCompra}><ShoppingCartOutlined style={{ fontSize: '25px' }} /></Button></Badge></div>} */}
                        {/* <div aria-hidden="true" style={{display: 'none'}}></div> */}


                    </Col>}
                    {!(props.mobile || props.tablet) &&
                        <Badge count={carrito.length}><Button onClick={showCompra}><ShoppingCartOutlined
                            style={{ fontSize: '25px' }} /></Button></Badge>}
                    {(props.mobile || props.tablet) && <Popover visible={showPopover} content=
                        {
                            <>
                                <Menu mode="vertical" items={menuNavegacion} onClick={onClickNavegacion}
                                    selectedKeys={[pagina]} />
                                <Badge count={carrito.length}><Button onClick={showCompra}><ShoppingCartOutlined
                                    style={{ fontSize: '25px' }} /></Button></Badge>
                            </>
                        }>
                        <MenuOutlined className="nav-phone-icon" onClick={handleShowMenu} />

                    </Popover>}


                </Row>
            </Header>
            {paginaRender(props.mobile)}
            <ChatBot steps={steps} floating={true} opened={false} />
            <Footer className="footer" style={{ textAlign: 'center' }}>PAGINA DESARROLLADA POR EL GRUPO 10 Dirección: Calle Girasoles
                1800 <br />Telefono 01-407629
                <div>
                <Button type="link" icon={<FacebookFilled/>} size="large" color='bluesky' href='https://www.facebook.com/SART-106127615473948'target='blank' />
                <Button type="link" icon={<TwitterOutlined/>} size="large" color='bluesky' href='https://twitter.com/SART45813400' target='blank'/>
                <Button type="link" icon={<InstagramOutlined/>} size="large" color='black' href='https://www.instagram.com/proyectosart/' target='blank'/>
                </div>
            </Footer>
        </Layout>
    );
}
